@import '../../../shared/styles/mixins';
@import '../../../shared/styles/variables';

body.modal {
  overflow: hidden;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $third-bg-color;
  backdrop-filter: blur(10px);
  z-index: 1200;
}

.modal {
  position: relative;
  width: 90%;
  max-height: 395px;
  padding: 40px 20px;
  margin: 0 auto;
  box-shadow: 7px 4px 14px rgba(0, 0, 0, 0.11);
  border-radius: 40px;
  background-color: $secondary-bg-color;

  @include media(tablet) {
    padding: 40px 80px;
  }
  @include media(desktop) {
    max-width: 704px;
  }
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  display: inline-block;
  width: 34px;
  height: 34px;
  background: $fifth-bg-color url('../../../shared/images/overused/cross.png')
    no-repeat 50%;
  background-size: 19px;
  border-radius: 50%;
  transition: $transition;

  &:hover {
    transform: scale(1.1);
  }

  @include media(tablet) {
    width: 44px;
    height: 44px;
  }
}

// ! Circle BTN

.circle {
  position: fixed;
  bottom: 30px;
  right: 20px;
  outline: none;
  background: $accent-color url('../../../shared/images/overused/add.png')
    no-repeat 50% 25%;
  background-size: 21px;
  width: 80px;
  height: 80px;
  color: $secondary-text-color;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid $accent-color;
  transition: $transition;
  z-index: 20;
  @include media(tablet) {
    position: relative;
    width: 44px;
    height: 44px;
    background-position: 50%;
  }

  &:hover {
    background-color: $hover-color;
    border-color: $hover-color;
  }

  & > span {
    font-weight: 500;
    font-size: 12px;
    width: 100%;
    height: 100%;
    line-height: 780%;

    @include media(tablet) {
      position: absolute;
      font-size: 20px;
      line-height: 27px;
      color: $fifth-text-color;
      line-height: normal;
      left: -85px;
      top: 50%;
      transform: translateY(-50%);
      width: 75px;
      height: fit-content;
    }
  }
}

// ! Long BTN

.long {
  outline: none;
  padding: 8px 80px;
  border-radius: 40px;
  cursor: pointer;
  border: 2px solid $accent-color;
  transition: $transition;
  background-color: $secondary-bg-color;
  color: $accent-color;
  // ???  box-shadow: 7px 4px 16px rgba(0, 0, 0, 0.08);

  &:hover {
    color: $hover-color;
    border-color: $hover-color;
  }
}

// ! Circle Info Section

.info {
  position: relative;
  outline: none;
  background: $accent-color url('../../../shared/images/overused/add.png')
    no-repeat 50%;
  background-size: 21px;
  width: 40px;
  height: 40px;
  color: $secondary-text-color;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid $accent-color;
  transition: $transition;

  &:hover {
    background-color: $hover-color;
    border-color: $hover-color;
  }

  & > span {
    position: absolute;
    font-weight: 500;
    font-size: 20px;
    color: black;
    line-height: normal;
    left: -85px;
    top: 50%;
    transform: translateY(-50%);
    width: 75px;
    height: fit-content;
  }
}
